import { observer } from 'mobx-react';
import * as React from 'react';
import styled from 'styled-components';

import Alert from '@vuukle/alert';
import Button from '@vuukle/button';
import Comment from '@vuukle/comment';
import Tag from '@vuukle/tag';
import TruncateText from '@vuukle/truncate-text';

import CommentModel from 'modules/CommentList/store/CommentItem';
import EditCommentBox from 'modules/EditCommentBox';
import WriteComment from 'modules/WriteComment';
import translation from 'stores/translationStore';

import { IAlert } from 'stores/models/Alert';

interface ICommentProps {
  comment: CommentModel;
  isModerator: boolean;
  /**
   * Highlight comment with border
   * @default false
   */
  highlighted?: boolean;

  className?: string;
}

const Wrapper = styled.div`
  margin: 10px 0;
  padding-bottom: 10px;
  &:not(:last-child) {
    border-bottom: 0.05rem solid ${(props) => (props.theme.isDark ? '#757575' : '#e7e9ed')};
  }
`;

const RepliesWrapper = styled('div')`
  margin-left: 15px;
  margin-top: 15px;
  ${Wrapper} {
    border-bottom: 0;
    padding-bottom: 0;
  }
`;

@observer
class CommentItem extends React.Component<ICommentProps> {
  public static defaultProps: Partial<ICommentProps> = {
    highlighted: false,
  };

  /**
   * @name getDropdownOptions
   * @description creates array of comment actions for dropdown
   */
  protected getDropdownOptions = () => {
    const options: Array<{ name: string; onClick: () => void }> = [
      {
        name: `${this.props.comment.collapsed ? translation.common.expand : translation.common.collapse}`,
        onClick: this.props.comment.collapse,
      },
    ];

    if (this.props.comment.isOwner) {
      options.unshift({
        name: 'Edit Comment',
        onClick: () => (this.props.comment.editMode = true),
      });
    }
    /** Detect if it's comment owner to show another comment actions */
    if (this.props.comment.isOwner) {
      options.unshift({
        name: translation.profile.removeComment,
        onClick: () => (window as any).open('https://vuukle.com/user/lock?to=comments', '_blank'),
      });
    } else {
      options.unshift({ name: translation.common.report, onClick: this.props.comment.report });
    }
    /** Add one more option to moderate comment */
    if (this.props.isModerator) {
      options.unshift({ name: 'Reject Comment', onClick: this.props.comment.reject });
    }
    return options;
  };

  /**
   * @name renderCommentHeader
   * @description get content for comment header
   */
  protected renderCommentHeader = (): JSX.Element | null => {
    const { authorType } = this.props.comment.data;
    /**
     * 1 - Admin
     * 2 - Author
     * 3 - Moderator
     */
    if (authorType === 1 || authorType === 2 || authorType === 3) {
      return (
        <Tag type="warning" key="mod" style={{ verticalAlign: 'top', margin: '0 5px' }}>
          {translation.common.moderator}
        </Tag>
      );
    }

    return null;
  };

  /**
   * Renders alert based on passed alert obj
   * @param {IAlert} alert - alert obj with message and type
   * @param {function} onClose - function to call once alert close button clicked
   */
  public renderAlert = (alert: IAlert, onClose: () => void) => (
    <Alert type={alert.type} onClose={onClose}>
      <div dangerouslySetInnerHTML={{ __html: alert.message }} />
    </Alert>
  );

  public render() {
    const { className, comment } = this.props;

    return (
      <Wrapper className={className}>
        {/** Top Alert for errors/warning */}
        {comment.topAlert.value && this.renderAlert(comment.topAlert.value, comment.topAlert.close)}

        {comment.editMode && (
          <EditCommentBox comment={this.props.comment} onClose={() => (this.props.comment.editMode = false)} />
        )}

        {!comment.editMode && !comment.hidden && (
          <Comment
            className="v-comment"
            name={comment.data.name}
            hash={comment.data.userId}
            onUserClick={comment.showCommenterProfile}
            avatar={comment.data.pictureUrl}
            comment={
              <TruncateText seeMore={translation.common.readMore}>
                <span dangerouslySetInnerHTML={{ __html: comment.getCommentTextForRender() }} />
              </TruncateText>
            }
            points={
              comment.data.userPoints
                ? `${comment.data.userPoints} ${
                    comment.data.userPoints === 1 ? translation.common.point : translation.common.points
                  }`
                : undefined
            }
            header={this.renderCommentHeader()}
            likes={comment.data.likeCount}
            dislikes={0}
            date={comment.data.timeago}
            minimize={comment.collapsed}
            actions={[
              <Comment.Action key="1">
                <Button type="subtle" onClick={comment.toggleLike} active={comment.currentSessionLiked} sm={true}>
                   <strong>{comment.data.likeCount}</strong>
                </Button>
              </Comment.Action>,
              <Comment.Share key="2" onClick={comment.share}>
                {translation.common.share}
              </Comment.Share>,
              <Comment.Action key="3">
                <a
                  href="#no"
                  onClick={(e) => {
                    e.preventDefault();
                    comment.toggleReplyBox();
                  }}
                >
                  {translation.common.reply /* Reply */}
                </a>
              </Comment.Action>,
            ]}
            options={this.getDropdownOptions()}
            highlighted={this.props.highlighted}
          />
        )}

        {/** Bottom Alert for errors/warning */}
        {comment.bottomAlert.value && this.renderAlert(comment.bottomAlert.value, comment.bottomAlert.close)}

        {/** Write comment box with sing in */}
        {comment.showReplyBox && (
          <div style={{ margin: '10px 0' }}>
            <WriteComment
              // TODO: mb rename this prop
              addComment={(commentItem: Comments.ServerComment) => {
                comment.addReply(commentItem); // Add reply to this comment
                comment.toggleReplyBox(); // Hide reply on success
              }}
              parentId={comment.data.id}
            />
          </div>
        )}
        {/* Replies */}
        {!comment.collapsed && comment.replies.length > 0 && (
          <RepliesWrapper>
            {comment.replies.map((reply: CommentModel) => (
              <CommentItem isModerator={this.props.isModerator} comment={reply} key={comment.data.id} />
            ))}
          </RepliesWrapper>
        )}
      </Wrapper>
    );
  }
}

export default CommentItem;
