import * as React from 'react';
import styled from 'styled-components';

import Button from '@vuukle/button';
import Tag from '@vuukle/tag';
import translation from 'stores/translationStore';

interface IProps {
  recommended: boolean;
  recommends: number;
  onClick: () => void;
  inProgress?: boolean;

  className?: string;
}

/**
 * @name RecommendButton
 * Button to recommend article.
 * Used in the header of the widget.
 * @render React
 * @example <RecommendButton recommended={false} recommends={15} onClick={() => alert('Recommended')} />
 */
const RecommendButton: React.FunctionComponent<IProps> = ({
  recommended,
  recommends,
  className,
  inProgress = false,
  onClick,
}) => {
  return (
    <Button sm={true} type="subtle" className={className} onClick={onClick} active={recommended} loading={inProgress}>
      <span role="img" aria-label="heart">
        
      </span>
      <span>{recommended ? translation.common.recommended : translation.common.recommend}</span> <Tag>{recommends}</Tag>
    </Button>
  );
};

const StyledRecommendButton = styled(RecommendButton)<IProps>`
  span {
    text-transform: capitalize !important;
    transition: all 0.2s;
  }
  span[role='img'] {
    margin-right: 2px;
    opacity: ${props => (props.recommended ? 1 : 0.5)};
  }
  &:hover {
    span[role='img'] {
      opacity: 1;
    }
  }
`;

export default StyledRecommendButton;
