import * as React from 'react';

import Input from '@vuukle/input';
import translation from 'stores/translationStore';

interface IProps {
  id: string;
  value: string;
  onChange: (e: React.FormEvent<HTMLInputElement>) => void;
  minLength?: number;
}

const PasswordInput: React.FunctionComponent<IProps> = ({ id, value, onChange, minLength = 5 }) => (
  <Input
    type="password"
    name="password"
    id={`password-${id}`}
    placeholder={translation.common.password}
    minLength={minLength}
    maxLength={32}
    onChange={onChange}
    required={true}
  />
);

export default PasswordInput;
