import * as React from 'react';
import styled from 'styled-components';

import Button from '@vuukle/button';
import Card from '@vuukle/card';
import Tag from '@vuukle/tag';
import translation from 'stores/translationStore';

interface IProps {
  articles: Articles.Article[];
  /**
   * Show or hide button to load more articles
   * @default false
   */
  showMoreButton: boolean;
  /**
   * On click handler for more button click
   */
  onMoreButtonClick: () => void;
  isVerticalCards?: boolean;
  className?: string;
}

const ArticlesWrapper: React.FunctionComponent<IProps> = ({
  articles,
  showMoreButton = false,
  onMoreButtonClick,
  isVerticalCards = false,
  className = '',
}) => {
  return (
    <div className={className}>
      <div>
        {articles.map((article) => (
          <Card
            key={article.articleId}
            heading={article.excerpt || article.title}
            img={article.articleAvatar}
            link={article.uri}
            linkRel="noopener nofollow"
            mode={isVerticalCards ? 'vertical' : 'horizontal'}
          >
            {article.commentCount > 0 && (
              <Tag style={{ marginRight: '5px' }}>
                {article.commentCount}{' '}
                {article.commentCount > 1 ? translation.commentText.whenX : translation.commentText.when1}
              </Tag>
            )}
          </Card>
        ))}
      </div>
      {showMoreButton && <Button onClick={onMoreButtonClick}>{translation.buttons.showMoreArticles}</Button>}
    </div>
  );
};

const StyledArticlesWrapper = styled(ArticlesWrapper)`
  > div {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  ${Button} {
    display: block;
    width: 100%;
  }

  ${Card} {
    flex: 0 49%;
    margin-bottom: 10px;
  }

  @media (max-width: 450px) {
    ${Card} {
      flex: 0 100%;
    }
  }
`;

export default StyledArticlesWrapper;
