import { inject, observer } from 'mobx-react';
import * as React from 'react';

import CommentStoreModel from 'modules/CommentList/store/CommentItem';

import EnglishBox from './components/EnglishBox';
import ForeignBox from './components/ForeignBox';

import EnglishBoxStore from './stores/EnglishBox';
import ForeignBoxStore from './stores/ForeignBox';

import FormWrapper from './components/FormWrapper';

interface IProps {
  /**
   * Add comment callback.
   * In general this prop is just a function which is used to add comment to the needed place in the store
   */
  addComment: (comment: Comments.ServerComment) => void;
  /**
   * It's id of the parent comment. Needed if we put reply box.
   * Use 0 for comment action (comment that appears as main in feed) and real `parentId` for an reply action
   */
  parentId: number;
  /**
   * Comment edit mode
   * @default false
   */
  editComment?: CommentStoreModel;
}

/** Injected Store Props */
import { UserStore } from 'modules/Auth/stores';
import { WidgetStore } from 'stores/widgetStore';
interface IInjectedProps extends IProps {
  userStore: UserStore;
  widgetStore: WidgetStore;
}

/**
 * @name HOCDecision
 * Decides which editor to show for user based on language publisher selected.
 * For english we show one editor and for non-english another with a transliteration plugin.
 * @render React
 * Main comment box:
 * @example <HOCDecision />
 * Reply box:
 * @example <HOCDecision addComment={(comment) => console.log('Comment has been added:', comment)} parentId={1} />}
 */
@inject('widgetStore', 'userStore')
@observer
class HOCDecision extends React.Component<IProps, {}> {
  public store: EnglishBoxStore | ForeignBoxStore;

  constructor(props: IProps) {
    super(props);

    this.store =
      this.injected.widgetStore.language !== 'en'
        ? new ForeignBoxStore(props.parentId, props.addComment)
        : new EnglishBoxStore(props.parentId, props.addComment);
  }

  get injected() {
    return this.props as IProps & IInjectedProps;
  }

  public renderCommentBox(): React.ReactElement | null {
    /** Decide which box we need to show based on publisher configuration */
    if (this.store instanceof ForeignBoxStore) {
      return <ForeignBox foreignStore={this.store} />;
    }

    return <EnglishBox boxStore={this.store} authorized={this.injected.userStore.isAuthorized} />;
  }

  public render() {
    return (
      <FormWrapper
        handleSubmit={this.store.onSubmit}
        id={this.store.id}
        inProgress={this.store.inProgress}
        alert={this.store.alert.value}
        closeAlert={this.store.alert.close}
        expand={() => (this.store.hidden = false)}
        isCollapsed={this.store.hidden}
        isReply={this.props.parentId !== 0}
        isDisabled={this.store.disabled}
        isClicked={!this.store.minimized}
      >
        {this.renderCommentBox()}
      </FormWrapper>
    );
  }
}

export default HOCDecision;
