import { observer } from 'mobx-react';
import React from 'react';
import ImageEntity from './ImagePreview';

interface IProps {
  /** Image remove action */
  items: string[];
  /** Function to remove entity */
  onRemove: (url: string) => void;
}

/**
 * Renders added images to comment
 */
export function ImagesPreview({ onRemove, items }: IProps) {
  return (
    <div>
      {items.map((url: string) => (
        <ImageEntity onRemove={onRemove} key={url} url={url} />
      ))}
    </div>
  );
}

export default observer(ImagesPreview);
