import * as Cookies from 'js-cookie';

export const getOptions = () => (process.env.NODE_ENV === 'production' ? { domain: '.vuukle.com' } : {});

/** Get user token */
export const getToken = () => Cookies.get(process.env.SESSION_COOKIE_NAME || 'token');
/** Set user token */
export const saveToken = (token: string) =>
  Cookies.set(process.env.SESSION_COOKIE_NAME || 'token', token, { ...getOptions(), expires: 365 });
/** Remove user token */
export const removeToken = () => Cookies.remove(process.env.SESSION_COOKIE_NAME || 'token', getOptions());
/** Save user API key for analytics usage (platform) */
export const setUserApiKey = (apiKey: string) => Cookies.set('uid', apiKey, { ...getOptions(), expires: 365 });
/** Remove user API key on sign out so analytics won't use old session */
export const removeUserApiKey = () => Cookies.remove('uid', getOptions());

export default {
  getToken,
  removeToken,
  removeUserApiKey,
  saveToken,
  setUserApiKey,
};
