import { action, computed, decorate, observable } from 'mobx';
import { createRef, RefObject } from 'react';

import { BoldIcon, CodeIcon, ItalicIcon, QuoteIcon, UlIcon, UnderlineIcon } from '../components/Icons';
import { whichTag } from '../utils';

export interface IOption {
  icon: any;
  onClick: () => void;
  title?: string;
  active?: boolean;
}

interface IFormattingState {
  bold: boolean;
  italic: boolean;
  underline: boolean;
  pre: boolean;
  ul: boolean;
  blockquote: boolean;
}

class Formatting {
  /** Ref to the contentEditable element */
  public ref: RefObject<HTMLDivElement> = createRef<HTMLDivElement>();
  /** State of the formatting things. i.e. bold: active/inactive etc. */
  public formattingState: IFormattingState;
  /** IE/Edge detection */
  private isEdgeBrowser: boolean = (document as any).documentMode || /Edge/.test(navigator.userAgent);

  constructor() {
    this.formattingState = this.getFormattingState();
  }

  @action
  private getFormattingState = (): IFormattingState => ({
    blockquote: whichTag('blockquote'),
    bold: document.queryCommandState('bold'),
    italic: document.queryCommandState('italic'),
    pre: whichTag('pre'),
    ul: document.queryCommandState('insertUnorderedList'),
    underline: document.queryCommandState('underline'),
  });

  @action
  private focus = () => {
    if (this.ref.current) {
      this.ref.current.focus();
    }
  };

  public updateFormattingState = () => (this.formattingState = this.getFormattingState());

  @action
  private formatText = (commandId: 'bold' | 'italic' | 'underline') => {
    this.focus();

    document.execCommand(commandId, false);
    this.formattingState = this.getFormattingState();

    this.focus();
  };

  // TODO: mb create a decorator
  // TODO: improve logic
  private formatBlock = (command: 'blockquote' | 'pre' | 'p'): void => {
    this.focus();

    // Firefox checking
    if (whichTag('blockquote')) {
      document.execCommand('outdent', false);
      if (command === 'blockquote') {
        return;
      }
    }

    if (document.queryCommandState('insertUnorderedList')) {
      document.execCommand('insertUnorderedList');
    }

    if (whichTag(command)) {
      document.execCommand('formatBlock', false, 'p');
    } else {
      if (document.queryCommandValue('formatBlock') && document.queryCommandValue('formatBlock') !== 'Normal') {
        // Edge returns 'Normal' for formatted blocks, so we need to check this too. Until they won't switch to chromium
        document.execCommand('formatBlock', false, 'p');
      }
      document.execCommand('formatBlock', false, `<${command}>`);
    }

    this.removeAttributes();
    this.updateFormattingState();
    this.focus();
  };

  /**
   * Clear HTML style attributes inside contentEditable
   * @return {void}
   */
  private removeAttributes = (): void => {
    if (this.ref.current) {
      this.ref.current.querySelectorAll('*').forEach((item) => {
        if (item instanceof HTMLElement) {
          item.removeAttribute('style');
        }
      });
    }
  };

  private insertList = (type: 'ol' | 'ul') => {
    this.focus();

    if (whichTag('blockquote')) {
      document.execCommand('outdent', false);
    }

    if (whichTag('pre') || whichTag('blockquote')) {
      this.formatBlock('p');
    }

    if (type === 'ol') {
      document.execCommand('insertOrderedList');
    } else {
      document.execCommand('insertUnorderedList');
    }

    this.removeAttributes();
    this.focus();
  };

  /**
   * Available formatting options to render
   * @return {IOption[]} Array of the options with all needed props to render using React
   */
  get options(): IOption[] {
    const { italic, bold, underline, blockquote, pre, ul } = this.formattingState;

    const options = [
      { active: bold, icon: BoldIcon, title: 'Bold', onClick: () => this.formatText('bold') },
      { active: italic, icon: ItalicIcon, onClick: () => this.formatText('italic'), title: 'Italic' },
      { active: underline, icon: UnderlineIcon, onClick: () => this.formatText('underline'), title: 'Underline' },
    ];

    // Unfortunately we can't offer block formatting inside Edge, as it's working unstable
    if (this.isEdgeBrowser) {
      return options;
    }

    // For all other browsers we offer block formatting options
    return [
      ...options,
      { active: blockquote, icon: QuoteIcon, onClick: () => this.formatBlock('blockquote'), title: 'Quotation' },
      { icon: CodeIcon, title: 'Code', onClick: () => this.formatBlock('pre'), active: pre },
      { icon: UlIcon, title: 'List', onClick: () => this.insertList('ul'), active: ul },
    ];
  }
}

decorate(Formatting, {
  formattingState: observable,
  updateFormattingState: action,

  options: computed,
});

export default Formatting;
