/**
 * index.ts
 *
 * This is the entry file for the application, only setup and boilerplate
 * code.
 */

import './polyfills';

import * as React from 'react';
import * as ReactDOM from 'react-dom';
import { initializeListener } from 'services/communication';
import widgetStore from 'stores/widgetStore';
import App from './containers/App';

const HTML_NODE = document.querySelector('html');
const BODY_NODE = document.querySelector('body');
const MOUNT_NODE = document.getElementById('app');

/* ========================================================
 *  Global Styles and Theming
======================================================== */
import { generateGlobalStyles, generateTheme } from '@vuukle/widget-theme';
import { ThemeProvider } from 'styled-components';
const theme = generateTheme(widgetStore.theme, widgetStore.language === 'ar');
const GlobalStyles = generateGlobalStyles(theme);

/* ========================================================
 *  Render React App
======================================================== */
const render = () =>
  ReactDOM.render(
    <ThemeProvider theme={theme}>
      <div>
        <GlobalStyles />
        <App />
      </div>
    </ThemeProvider>,
    MOUNT_NODE
  );

render();

/* ========================================================
* Detect APP language
======================================================== */
if (HTML_NODE !== null) {
  if (widgetStore.language === 'ar') {
    HTML_NODE.setAttribute('dir', 'rtl');
  }
  if (widgetStore.language !== 'en') {
    HTML_NODE.setAttribute('lang', widgetStore.language);
  }
}

/* ========================================================
* Check for Opera Mini in Saving mode
======================================================== */
if (
  navigator.userAgent.indexOf('Opera Mini') !== -1 ||
  Object.prototype.toString.call((window as any).operamini) === '[object OperaMini]'
) {
  const element = document.createElement('div');
  element.style.border = '1px solid #ffb995';
  element.style.backgroundColor = '#fff8f4';
  element.style.padding = '8px';
  element.style.margin = '10px';
  element.innerHTML = 'Please disable Opera Mini "Saving mode" to have a correct working page and comments widget.';
  // Insert to body
  if (BODY_NODE) {
    BODY_NODE.insertBefore(element, BODY_NODE.firstChild); // insert message
  }
}

/* ========================================================
*  Listen for messages from porthole
* [replace to another file if we will add more handlers]
======================================================== */
initializeListener();

/* ========================================================
*  HOT reload
======================================================== */
if ((module as any).hot && process.env.NODE_ENV === 'development' && MOUNT_NODE instanceof HTMLElement) {
  // Hot reloadable React components and translation json files
  // modules.hot.accept does not accept dynamic dependencies,
  // have to be constants at compile-time
  (module as any).hot.accept('./containers/App', () => {
    ReactDOM.unmountComponentAtNode(MOUNT_NODE);
    render();
  });
}

/* ========================================================
 Ohh! And one more... we need to watch height of app since
 we will be rendered inside iframe
======================================================== */
let oldHeight = MOUNT_NODE instanceof HTMLElement ? MOUNT_NODE.offsetHeight : 0;

setInterval(() => {
  // We add 5 pixels to height because we can have for example in bottom CircleProgress and while it will
  // be animated scrollbar will appear/disappear because animation is faster than this height processing
  const newHeight: number = MOUNT_NODE instanceof HTMLElement ? MOUNT_NODE.offsetHeight + 5 : 0;

  if (newHeight !== oldHeight) {
    if (window.parent.postMessage) {
      oldHeight = newHeight;

      window.parent.postMessage(
        JSON.stringify({
          data: { height: newHeight },
          sourceOrigin: (window as any).origin,
          sourceWindowName: window.name,
          type: 'resize',
          widget: 'emotes',
        }),
        '*'
      );
    }
  }
  // tslint:disable-next-line
}, 200);

/* ========================================================
* and one more...  Log Vuukle Initialization
======================================================== */
// tslint:disable-next-line
console.log(
  `%c[VUUKLE] Comments widget initialized! Version: ${
    process.env.VERSION
  }. Need help? Reach us at support[at]vuukle[dot]com`,
  'color:#039BE5;'
);
