import { inject, observer } from 'mobx-react';
import * as React from 'react';

import Alert from '@vuukle/alert';
import Button from '@vuukle/button';

import translation from 'stores/translationStore';

import ProfileRow from 'components/ProfileRow';
import Auth from 'modules/Auth';
import { openModalWidgetProfile } from 'services/communication';

export interface IProps {
  /** Function which will be called on form submit */
  handleSubmit: (e: React.FormEvent<HTMLFormElement>) => void;
  /** Comment box id */
  id: string;
  /** Form/Comment submit is in progress */
  inProgress: boolean;
  /** Alert object to show message */
  alert: { type: 'success' | 'warning' | 'error'; message: string } | null;
  /** Function to close alert. So we allow users to close alert earlier */
  closeAlert: () => void;
  /** If collapsed then only button 'Write Comment' shown first which expands box on click */
  isCollapsed: boolean;
  /** Expand function for isCollapsed: true */
  expand: () => void;
  /** if block is main or opened inside reply. Needed for some logic things. */
  isReply?: boolean;
  /** Commenting Disabled */
  isDisabled: boolean;
  /** Textarea was clicked (User wants to write comment) and we can show additional UI items */
  isClicked: boolean;
}

/** Injected MobX Stores Props */
import { AuthStore, UserStore } from 'modules/Auth/stores';
interface IInjectedProps extends IProps {
  userStore: UserStore;
  authStore: AuthStore;
}

@inject('userStore', 'authStore')
@observer
class FormWrapper extends React.Component<IProps, {}> {
  /** Injected stores */
  get injected() {
    return this.props as IInjectedProps;
  }

  /**
   * Sends message to the platform to show comments widget with needed params
   * @return {void}
   */
  protected openProfileModal = (): void =>
    openModalWidgetProfile({
      avatar: (this.injected.userStore.details as User).avatar,
      id: (this.injected.userStore.details as User).id,
      name: (this.injected.userStore.details as User).name,
      points: (this.injected.userStore.details as User).points,
    });

  public render() {
    /** Commenting has been disabled by publisher */
    if (this.props.isDisabled) {
      return <Alert>{translation.messages.commentsClosed /* Comments are now closed. */}</Alert>;
    }

    /** Check if we need to collapse main comment box on widget load. */
    if (this.props.isCollapsed && !this.props.isReply) {
      return (
        <Button onClick={this.props.expand} style={{ width: '100%' }}>
          {translation.buttons.addComment /* Add Comment */}
        </Button>
      );
    }

    return (
      <div>
        <form onSubmit={this.props.handleSubmit}>
          {this.injected.userStore.details && (
            <ProfileRow
              profile={this.injected.userStore.details}
              showComments={this.openProfileModal}
              signOut={this.injected.authStore.withSSO ? undefined : this.injected.authStore.signOut}
            />
          )}
          {this.props.children}
          <div style={{ marginTop: '10px' }}>
            {this.props.alert && (
              <Alert type={this.props.alert.type} onClose={this.props.closeAlert}>
                {this.props.alert.message}
              </Alert>
            )}
            {this.injected.userStore.isAuthorized && (
              <div style={{ textAlign: 'right' }}>
                <Button loading={this.props.inProgress} htmlType="submit">
                  {translation.buttons.post}
                </Button>
              </div>
            )}
          </div>
        </form>
        {this.props.isClicked && <Auth />}
      </div>
    );
  }
}

export default FormWrapper;
