/** Detect Tag in a content editable */
export const whichTag = (tagName: string) => {
  const sel = window.getSelection();
  let containerNode: Node | null = null;
  let tagFound = false;
  tagName = tagName.toUpperCase();

  if (sel.rangeCount > 0) {
    containerNode = sel.getRangeAt(0).commonAncestorContainer;
  }

  while (containerNode) {
    if (containerNode.nodeType === 1 && (containerNode as HTMLElement).tagName === tagName) {
      tagFound = true;
      containerNode = null;
      break;
    } else if ((containerNode as HTMLElement).contentEditable === 'true') {
      tagFound = false;
      break;
    } else {
      containerNode = containerNode.parentNode;
    }
  }

  return tagFound;
};
