import React from 'react';
import styled from 'styled-components';

import { Menu } from '@vuukle/menu';
import GIFPicker from 'modules/GifPicker';
import { Button } from '../Options';
import Icon from './Icon';

interface IProps {
  onSelect: (GIFUrl: string) => void;
  disabled?: boolean;
}

const Wrapper = styled.div`
  position: relative;
  .menu {
    position: absolute;
    left: 0;
    top: 20px;
    width: 350px;
    padding: 10px;
  }
`;

class GIFDropdown extends React.Component<IProps, { displayMenu: boolean }> {
  public wrapperRef: React.RefObject<any> = React.createRef();
  public state = { displayMenu: false };

  public showMenu = (event: React.MouseEvent<HTMLButtonElement>) => {
    event.preventDefault();
    this.setState({ displayMenu: true }, () => document.addEventListener('click', this.hideMenu));
  };

  public hideMenu = (event: React.MouseEvent<HTMLButtonElement> | MouseEvent): void => {
    const button = this.wrapperRef.current.querySelector('button');
    if (
      (this.wrapperRef.current && !this.wrapperRef.current.contains(event.target)) ||
      (button && button.contains(event.target))
    ) {
      this.setState({ displayMenu: false }, () => document.removeEventListener('click', this.hideMenu));
    }
  };

  /** Render Dropdown Menu once it's opened */
  private renderMenu = () => (
    <Menu className="menu">
      <GIFPicker
        onSelect={(GIFLink) => {
          this.setState({ displayMenu: false });
          this.props.onSelect(GIFLink);
        }}
      />
    </Menu>
  );

  public render() {
    return (
      <Wrapper ref={this.wrapperRef}>
        <Button type={'button'} onClick={this.showMenu} disabled={this.props.disabled}>
          <Icon />
        </Button>

        {/** GIF picker component */}
        {this.state.displayMenu && this.renderMenu()}
      </Wrapper>
    );
  }
}

export default GIFDropdown;
