import { isOffensive, isValidEmail, isValidName } from '@vuukle/utils';
import { action, computed, decorate, observable } from 'mobx';
import { authAPIs } from 'services/apis';
import { callSSOMethod, reportEvent } from 'services/communication';
import urlSearchParams from 'services/urlSearchParams';
import translation from 'stores/translationStore';
import userStore from './userStore';

export class AuthStore {
  /** Determines if API is in progress and we are waiting for data */
  public inProgress: boolean = false;
  /** Error happened */
  public error: string | null = null;
  /** Configurable values */
  public readonly facebookEnabled: boolean = urlSearchParams.get('fAuth') !== 'false'; // show social auth
  public readonly googleEnabled: boolean = urlSearchParams.get('gAuth') !== 'false';
  public readonly twitterEnabled: boolean = urlSearchParams.get('tAuth') !== 'false';
  public readonly disqusEnabled: boolean = urlSearchParams.get('dAuth') !== 'false';
  public readonly withVuukle: boolean = urlSearchParams.get('vuukleAuth') !== 'false'; // show Vuukle auth
  public readonly withPassword: boolean = urlSearchParams.get('passwordAuth') === 'true'; // Password Auth + Available form to sign up
  /** Publisher SSO(auth method) is enabled only if everything is turned off */
  public get withSSO() {
    return (
      !this.facebookEnabled && !this.googleEnabled && !this.twitterEnabled && !this.disqusEnabled && !this.withVuukle
    );
  }

  constructor() {
    /** Handler for publishers using widget inside webView to authorize users */
    (window as any).signInUser = (name: any, email: any, avatar: any = '') => {
      if (
        typeof name === 'string' &&
        typeof email === 'string' &&
        name.length > 2 &&
        email.length > 3 &&
        !userStore.isAuthorized
      ) {
        this.signInWithoutPassword(name, email, avatar);
      }
    };
  }

  /**
   * @name signOut
   * @description remove user session and token
   * @see {@link userStore#forgetUser}
   * @return {void}
   */
  public signOut = userStore.forgetUser;

  /** Detect if cookies allowed */
  get isCookiesAllowed() {
    document.cookie = 'vuukle_cookie_test=true';
    const cookiesAllowed = document.cookie.indexOf('vuukle_cookie_test') !== -1;
    document.cookie = 'vuukle_cookie_test=true; expires=Thu, 01-Jan-1970 00:00:01 GMT';
    return cookiesAllowed;
  }

  /**
   * @public
   * @name signIn
   * @description Login user using API
   * @param {string} email
   * @param {string} password
   * @return {Promise<boolean>} - true or false based if process is successful or not
   */
  public signIn = async (email: string, password: string): Promise<boolean> => {
    this.inProgress = true;
    this.error = null;
    try {
      /** Check CORS limits */
      if (!this.isCookiesAllowed) {
        await this.openCORSModal();
      }

      const response = await authAPIs.withPassword(email, password);
      if (!response.isPasswordEntered || Array.isArray(response.errors)) {
        throw new Error(translation.messages.incorrectDetails);
      }

      userStore.token = response.token;
      await userStore.authorizeWithToken();
      return true;
    } catch (error) {
      this.error = error.message;
      return false;
    } finally {
      this.inProgress = false;
    }
  };

  /**
   * @public
   * @name signInWithoutPassword
   * @description User login method without password
   * @param {string} name
   * @param {string} email
   * @param {string} avatar
   * @return {Promise<boolean>} - `true` is everything is successful and `false` if something failed
   */
  public signInWithoutPassword = async (name: string, email: string, avatar: string = ''): Promise<boolean> => {
    this.inProgress = true;
    this.error = null;

    name = name.trim();
    email = email.trim();

    try {
      /** Check CORS limits */
      if (!this.isCookiesAllowed) {
        await this.openCORSModal();
      }

      await this.validateFields(name, email);
      const response = await authAPIs.withoutPassword(name, email, avatar);

      userStore.token = response.data.authTicket.token;
      await userStore.authorizeWithToken();
      return true;
    } catch (error) {
      this.error = error.message;
      return false;
    } finally {
      this.inProgress = false;
    }
  };

  /**
   * @public
   * @name signUp
   * @description Sign up user by validating and sending data to server
   * @param {string} name
   * @param {string} email
   * @param {string} password
   */
  public signUp = async (name: string, email: string, password: string): Promise<boolean> => {
    this.inProgress = true;
    this.error = null;

    name = name.trim();
    email = email.trim();

    try {
      /** Check CORS limits */
      if (!this.isCookiesAllowed) {
        await this.openCORSModal();
      }

      await this.validateFields(name, email);
      const response = await authAPIs.register(name, email, password);
      if (response.success) {
        userStore.token = response.data.authTicket.token;
        return userStore.authorizeWithToken();
      } else {
        throw new Error(response.errors[0] || 'Please try again later');
      }
    } catch (error) {
      this.error = error.message;
      return false;
    } finally {
      this.inProgress = false;
    }
  };

  /**
   * @private
   * @name openCORSModal
   * @description This is method is used to try to bypass cors limit in some browsers
   * for example our iframe can't use cookies while our site wasn't visited, we open modal to force user
   * to visit site and then we can work with cookies
   */
  private openCORSModal = async () => {
    const authModal = window.open(
      `${window.location.origin}${process.env.AUTH_CONFIRMATION_HTML}`,
      'authVerificationModal',
      'location=0,status=0,width=800,height=400'
    );

    return new Promise((resolve, reject) => {
      const oauthInterval = window.setInterval(() => {
        if (authModal && authModal.closed) {
          window.clearInterval(oauthInterval);
          resolve();
        }
      }, 1000);
    });
  };

  /**
   * @public
   * @description Open modal window to authorize user
   * @param {'facebook' | 'google' | 'twitter'} network - social network to log in with
   * @return {void}
   */
  public signInWithSocial = (network: 'facebook' | 'google' | 'twitter' | 'disqus'): void => {
    reportEvent(`login_${network}` as 'login_facebook' | 'login_google' | 'login_twitter' | 'login_disqus');

    const options = {
      callback: () => userStore.authorizeWithToken(),
      path: '',
    };

    switch (network) {
      case 'google':
        options.path = process.env.GOOGLE_LOGIN_LINK || '';
        break;
      case 'twitter':
        options.path = process.env.TWITTER_LOGIN_LINK || '';
        break;
      case 'disqus':
        options.path = process.env.DISQUS_LOGIN_LINK || '';
        break;
      default:
        options.path = process.env.FACEBOOK_LOGIN_LINK || '';
        break;
    }

    const oauthWindow = window.open(options.path, 'socialAuth', 'location=0,status=0,width=800,height=400');
    const oauthInterval = window.setInterval(() => {
      if (oauthWindow && oauthWindow.closed) {
        window.clearInterval(oauthInterval);
        options.callback();
      }
      // tslint:disable-next-line
    }, 1000);
  };

  /**
   * @name callSSO
   * @description sending message to platform.js with command to open SSO method
   * @return {void}
   */
  public openSSO = (): void => callSSOMethod();

  /**
   * @name closeAlert
   * @description Remove alert
   * @return {void}
   */
  public closeAlert = (): void => {
    this.error = null;
  };

  /**
   * @private
   * @name validateFields
   * @description validate fields before calling APIs which might create new user
   * @param {string} name
   * @param {string} email
   */
  private validateFields = (name: string, email: string) =>
    new Promise((resolve, reject) => {
      if (!isValidName(name) || isOffensive(name)) {
        reject(new Error(translation.common.invalidName));
      }
      if (!isValidEmail(email)) {
        reject(new Error(translation.common.invalidEmail));
      }
      resolve();
    });
}

decorate(AuthStore, {
  error: observable,
  inProgress: observable,
  withSSO: computed,

  closeAlert: action,
  signIn: action,
  signInWithoutPassword: action,
  signOut: action,
  signUp: action,
});

export default new AuthStore();
