import { observer } from 'mobx-react';
import React from 'react';

import Tag from '@vuukle/tag';
import TextareaFooter from 'components/TextareaFooter';
import RichEditor, { IEditorChangeReturn } from 'modules/RichEditor';
import ToxicityMeter from './ToxicityMeter';

import translation from 'stores/translationStore';

/** Component Props */
import EnglishEditBoxStore from 'modules/EditCommentBox/stores/EnglishEditBox';
import EnglishBoxStore from '../stores/EnglishBox';
interface IProps {
  boxStore: EnglishBoxStore | EnglishEditBoxStore;
  authorized: boolean;
}

@observer
class WriteCommentBox extends React.Component<IProps> {
  constructor(props: IProps) {
    super(props);
  }

  /**
   * Handler for RichEditor change
   * @return {void}
   */
  public onEditorChange = ({ html }: IEditorChangeReturn): void => {
    this.props.boxStore.value = html;
  };

  /** Add Image */
  public addImage = (image: string | File) => {
    if (image instanceof File) {
      this.props.boxStore.images.uploadImage(image);
    } else {
      this.props.boxStore.images.add(image);
    }
  };

  /** Returns main content of the form to render */
  private renderContent(): React.ReactElement[] {
    return [
      <RichEditor
        id={this.props.boxStore.id}
        value={this.props.boxStore.value}
        placeholder={translation.common.writeComment /* Write Comment */}
        onFocus={this.props.boxStore.onFocus}
        onBlur={this.props.boxStore.onBlur}
        onChange={this.onEditorChange}
        imagesDisabled={this.props.boxStore.images.inProgress || this.props.boxStore.images.uploadLimitExceeded}
        hideUploadImage={!this.props.authorized}
        addImage={this.addImage}
        removeImage={this.props.boxStore.images.remove}
        images={this.props.boxStore.images.items}
        disabled={this.props.boxStore.inProgress}
        rows={!this.props.boxStore.minimized ? 4 : 2}
        showFormattingRow={!this.props.boxStore.minimized}
        key={0}
      />,
      <TextareaFooter focused={this.props.boxStore.focused} key={1}>
        <div>
          {/* Show toxicity limit only if it's appropriate */}
          {this.props.boxStore.toxicityModel.limit < 100 && (
            <ToxicityMeter
              value={this.props.boxStore.toxicityModel.value}
              loading={this.props.boxStore.toxicityModel.loading}
              toxicityText={translation.toxicity.long}
              visibilityLimit={this.props.boxStore.toxicityModel.limit}
            />
          )}
        </div>
        <div>
          {this.props.boxStore.allowedCharactersCount <= 200 && <Tag>{this.props.boxStore.allowedCharactersCount}</Tag>}
        </div>
      </TextareaFooter>,
    ];
  }

  public render() {
    return <div>{this.renderContent()}</div>;
  }
}

export default WriteCommentBox;
