import React from 'react';
import styled from 'styled-components';

interface IButtonProps {
  active?: boolean;
  disabled?: boolean;
  onClick?: (e: React.MouseEvent<HTMLButtonElement>) => void;
  /** Needed for rendering element as <label> */
  for?: string;
}

export const Button = styled.button<IButtonProps>`
  -webkit-appearance: none !important;
  outline: none;
  border: none;
  background: transparent;
  transition: color 0.2s;
  cursor: pointer;
  margin: 0;
  padding: 0 5px;

  &:hover {
    svg {
      fill: ${(props) => props.theme.textColor};
      transition: all 0.3s;
    }
  }

  svg {
    max-width: 20px;
    width: 100%;
    fill: ${(props) => props.theme.mutedColor};
    ${(props) => props.disabled && `fill: ${props.theme.mutedColor} !important`};
  }

  //min-width: 20px;

  ${(props) =>
    props.active &&
    `
    color: ${props.theme.textColor};
    font-weight: bold;
    svg {
      fill: ${props.theme.textColor};
    }
  `};

  ${(props) => props.disabled && `cursor: not-allowed;`}
`;

const Wrapper = styled.div`
  display: flex;
  justify-content: flex-start;
  flex-direction: row;
  height: 30px;
  background: ${(props) => props.theme.input.background};
  padding: 0 4px;
  border: 0;
  border-top: 1px dashed ${(props) => props.theme.input.border};
  align-items: center;
`;

export interface IOption {
  onClick: () => void;
  icon: string;
  title?: string;
  active?: boolean;
}

interface IProps {
  children?: any;
  options: IOption[];
}

const Options = ({ options, children }: IProps) => {
  return (
    <Wrapper>
      {children}
      <span>&nbsp;</span>
      {options.map((option) => (
        <Button
          dangerouslySetInnerHTML={{ __html: option.icon }}
          onClick={option.onClick}
          active={option.active}
          title={option.title}
          type="button"
          key={option.title}
        />
      ))}
    </Wrapper>
  );
};

export default Options;
