/**
 * @file Initialize widget communication listener.
 * Many of the messages can change our state, so we import states here and change them as it's easier to setup just
 * one listener and control everything within it
 */
import { makeDevLog } from '@vuukle/utils';
import path from 'ramda/src/path';
import { windowProxy } from 'services/communication';

import { authStore, userStore } from 'modules/Auth/stores';
import CommentsStore from 'modules/CommentList/store';
import widgetStore from 'stores/widgetStore';

export function initializeListener() {
  windowProxy.addEventListener((event: any) => {
    if (!(event.data instanceof Object)) {
      return; // Message is invalid
    }

    makeDevLog('log', 'comments porthole event received', event);

    if (path(['data', 'authenticateUser'], event) instanceof Object) {
      /** Authenticate user */
      authenticateUser(event.data.authenticateUser);
    } else if (path(['data', 'modalData', 'auth'], event)) {
      /** Modal Authorization */
      userStore.authorizeSilently();
    } else if (path(['data', 'syncWidget', 'votes'], event)) {
      /** Sync recommendations based on information from other widgets */
      widgetStore.recommendCount = event.data.syncWidget.votes;
      widgetStore.isRecommended = event.data.syncWidget.voted;
    } else if (path(['data', 'loadComment'], event)) {
      CommentsStore.getCommentDetailsByID(event.data.loadComment);
    }
  });
}

/** Authenticate user based on received message */
function authenticateUser(authenticationObject: any) {
  const { name, email, avatar, token, signOut } = authenticationObject;

  // Sign out user if called with signOut key
  if (signOut) {
    return authStore.signOut();
  }

  if (
    typeof name === 'string' &&
    typeof email === 'string' &&
    name.length > 2 &&
    email.length > 3 &&
    !userStore.isAuthorized
  ) {
    // Authenticate user with name and email
    authStore.signInWithoutPassword(name, email, avatar);
  } else if (typeof token === 'string') {
    // Authenticate user with token
    userStore.token = token;
    userStore.authorizeWithToken();
  }
}
