import { inject, observer } from 'mobx-react';
import * as React from 'react';
import styled from 'styled-components';

import { CommentsStore } from 'modules/CommentList/store';
import { WidgetStore } from 'stores/widgetStore';

import RecommendButton from 'components/RecommendButton';
import translation from 'stores/translationStore';

const Wrapper = styled.header`
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 5px;
  margin-bottom: 15px;
  margin-top: 5px;
  border-bottom: 1px solid rgba(150, 150, 150, 0.2);
  > div:first-child {
    font-weight: 600;
  }
`;

interface IProps {
  className?: string;
}

interface IInjectedProps {
  commentsStore: CommentsStore;
  widgetStore: WidgetStore;
}

@inject('commentsStore', 'widgetStore')
@observer
class Header extends React.Component<IProps> {
  get injected() {
    return this.props as IProps & IInjectedProps;
  }

  public render() {
    if (this.injected.commentsStore.loading && this.injected.commentsStore.totalComments <= 0) {
      return null;
    }

    return (
      <Wrapper className={this.props.className}>
        <div>
          {this.injected.commentsStore.totalComments === 1
            ? translation.commentText.when1
            : translation.commentText.whenX}{' '}
          ({this.injected.commentsStore.totalComments})
        </div>
        <div>
          <RecommendButton
            recommended={this.injected.widgetStore.isRecommended}
            recommends={this.injected.widgetStore.recommendCount}
            onClick={this.injected.widgetStore.recommend}
            inProgress={this.injected.widgetStore.recommendInProgress}
          />
        </div>
      </Wrapper>
    );
  }
}

export default Header;
