import debounce from 'lodash/debounce';
import React from 'react';
import styled from 'styled-components';

import Input from '@vuukle/input';

import GifItem from './GifItem';
import { getSearchGIFs, getTrendingGIFs, IGIFItem } from './utils';

interface IProps {
  onSelect: (GIFLink: string) => void;
}

interface IState {
  GIFs: IGIFItem[];
  searchGIFs: IGIFItem[];
  searchValue: string;
}

const SearchResult = styled.div`
  margin-top: 5px;
  margin-right: -5px;
  height: 250px;
  overflow-y: auto;
  > p {
    font-size: 0.8em;
    color: ${(props) => props.theme.mutedColor};
    text-transform: uppercase;
    font-weight: bold;
    margin: 0 0 10px;
  }
  > div {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
`;

class GifPicker extends React.Component<IProps, IState> {
  constructor(props: IProps) {
    super(props);
    this.state = {
      /** Trending GIFs from Giphy */
      GIFs: [],
      /** GIFs based on search value from Giphy */
      searchGIFs: [],
      /** Search input value */
      searchValue: '',
    };
  }

  /**
   * Lifecycle method.
   * componentDidMount() is invoked immediately after a component is mounted (inserted into the tree).
   */
  public componentDidMount() {
    this.getTrending();
  }

  /**
   * Search input change handler
   * @param {React.FormEvent<HTMLInputElement>} e - Input event
   * @return {void}
   */
  private onSearchInputChange = (e: React.FormEvent<HTMLInputElement>) =>
    this.setState({ searchValue: e.currentTarget.value || '' }, () => this.searchGIFs());

  /**
   * Get trending GIFs and update state
   * @return {Promise<void>}
   */
  private getTrending = async (): Promise<void> => {
    const trendingResponse = await getTrendingGIFs();
    this.setState({ GIFs: trendingResponse.data });
  };

  /**
   * Get GIFs based on search input value. If it's empty then get trending GIFs.
   * @return {Promise<void>}
   */
  private searchGIFs = debounce(async (): Promise<void> => {
    if (this.state.searchValue) {
      const searchResponse = await getSearchGIFs(this.state.searchValue);
      this.setState({ searchGIFs: searchResponse.data });
    } else {
      this.getTrending().then(() => this.setState({ searchGIFs: [] }));
    }
  }, 1000);

  /** Renders GIF items from the Giphy response */
  private renderGifItems = (GIFs: IGIFItem[]): JSX.Element[] =>
    GIFs.map((gif: IGIFItem) => (
      <GifItem
        key={gif.id}
        image={gif.images.fixed_height_small.url}
        onClick={() => this.props.onSelect(gif.images.downsized_medium.url)}
      />
    ));

  public render() {
    /** Select GIFs array to render based on search input */
    const GIFs = this.state.searchGIFs.length > 0 ? this.state.searchGIFs : this.state.GIFs;

    return (
      <div>
        <div>
          <Input
            placeholder="Search GIFs via Giphy ..."
            value={this.state.searchValue}
            onChange={this.onSearchInputChange}
          />
        </div>
        <SearchResult>
          <p>{this.state.searchGIFs.length ? 'SEARCH RESULTS' : 'TRENDING'}</p>
          <div>{this.renderGifItems(GIFs)}</div>
        </SearchResult>
      </div>
    );
  }
}

export default GifPicker;
