import * as React from 'react';

import Input from '@vuukle/input';
import translation from 'stores/translationStore';

interface IProps {
  id: string;
  value: string;
  onChange: (e: React.FormEvent<HTMLInputElement>) => void;
  disabled?: boolean;
}

const EmailInput: React.FunctionComponent<IProps> = ({ id, value, onChange, disabled = false }) => (
  <Input
    type="email"
    value={value}
    placeholder={translation.common.email}
    dir="auto"
    name="email"
    id={`email-${id}`}
    onChange={onChange}
    required={true}
    disabled={disabled}
  />
);

export default EmailInput;
