import { inject, observer } from 'mobx-react';
import * as React from 'react';

import Button from '@vuukle/button';
import Loader from '@vuukle/loader';

import translation from 'stores/translationStore';
import CommentModel from './store/CommentItem';

import SortingRow from 'components/SortingRow';
import Comment from './components/CommentItem';

/** MobX injected props */
import { UserStore } from 'modules/Auth/stores';
import { CommentsStore } from './store';
interface IInjectedProps {
  commentsStore: CommentsStore;
  userStore: UserStore;
}

@inject('commentsStore', 'userStore')
@observer
export default class Comments extends React.Component {
  get injected() {
    return this.props as IInjectedProps;
  }

  public render() {
    const { comments, loading, totalComments, moreAvailable, sorting, selectedComment } = this.injected.commentsStore;
    const isModerator = (this.injected.userStore.details && this.injected.userStore.details.isModerator) || false;

    if (loading && totalComments <= 0) {
      return <Loader size="15px" style={{ margin: '20px auto' }} />;
    }

    if (totalComments <= 0) {
      return <p style={{ textAlign: 'center' }}>{translation.messages.noComments}</p>;
    }

    return (
      <div>
        <SortingRow active={sorting} onSelect={this.injected.commentsStore.changeSorting} />
        <div style={{ margin: '15px 0' }}>
          {selectedComment && <Comment comment={selectedComment} isModerator={isModerator} highlighted={true} />}
          {loading && comments.length <= 0 && <Loader size="15px" />}
          {!loading && comments.length <= 0 && (
            <p style={{ textAlign: 'center' }}>{translation.messages.noCommentsInSorting}</p>
          )}
          {comments.map((comment: CommentModel) => (
            <Comment comment={comment} isModerator={isModerator} key={comment.data.id} />
          ))}
        </div>
        {moreAvailable && (
          <Button
            onClick={this.injected.commentsStore.loadMoreComments}
            loading={loading}
            style={{ display: 'block', width: '100%' }}
          >
            {translation.buttons.loadMore /* Load More */}
          </Button>
        )}
      </div>
    );
  }
}
