/**
 * @file Component to use for users authorization without password
 */
import Alert from '@vuukle/alert';
import Button from '@vuukle/button';
import uniqueId from 'lodash/uniqueId';
import { inject, observer } from 'mobx-react';
import * as React from 'react';

import { AuthStore } from 'modules/Auth/stores';
import translation from 'stores/translationStore';

import EmailInput from './components/EmailInput';
import NameInput from './components/NameInput';
import PrivacyCheckbox from './components/PrivacyCheckbox';
import StyledForm from './components/StyledForm';

interface IInjectedProps {
  authStore: AuthStore;
}

interface IState {
  /** Name input value */
  name: string;
  /** Email input value */
  email: string;
}

@inject('authStore')
@observer
class PasswordLessAuth extends React.Component<{}, IState> {
  public readonly state: Readonly<IState> = {
    email: '',
    name: '',
  };
  private id: string = uniqueId('pwdless');

  /**
   * @name injected
   * @description returns injected props by MobX
   */
  get injected(): IInjectedProps {
    return this.props as IInjectedProps;
  }

  /**
   * @name onSubmit
   * @protected
   * @description form submit event.
   * @param {React.FormEvent<HTMLFormElement>} e - Form event
   * @returns {void}
   */
  protected onSubmit = async (e: React.FormEvent<HTMLFormElement>): Promise<void> => {
    e.preventDefault();

    await this.injected.authStore.signInWithoutPassword(this.state.name, this.state.email);
    if (!this.injected.authStore.error) {
      this.setState({ email: '', name: '' });
    }
  };

  public render() {
    const {
      authStore: { closeAlert, error, inProgress },
    } = this.injected;

    return (
      <StyledForm onSubmit={this.onSubmit} action="#">
        <NameInput
          id={this.id}
          value={this.state.name}
          onChange={(e: React.FormEvent<HTMLInputElement>) => this.setState({ name: e.currentTarget.value })}
        />
        <EmailInput
          id={this.id}
          value={this.state.email}
          onChange={(e: React.FormEvent<HTMLInputElement>) => this.setState({ email: e.currentTarget.value })}
        />
        <PrivacyCheckbox id={this.id} />
        {/* Errors Alert */}
        {error && (
          <Alert type="error" onClose={closeAlert}>
            {this.injected.authStore.error}
          </Alert>
        )}
        {/* Submit Button */}
        <div style={{ textAlign: 'right' }}>
          <Button loading={inProgress} htmlType="submit">
            {translation.common.signIn}
          </Button>
        </div>
      </StyledForm>
    );
  }
}

export default PasswordLessAuth;
