import uniqueId from 'lodash/uniqueId';
import * as React from 'react';
import styled from 'styled-components';

import { Button } from '../Options';
import CameraIcon from './CameraIcon';

interface IProps {
  className?: string;
  onFileInputChange: (e: any) => void;
  disabled?: boolean;
}

const UploadButton = ({ className, onFileInputChange, disabled }: IProps) => {
  const id = uniqueId('upload_');
  return (
    <div className={className}>
      <Button type="button" as="label" for={id} style={{ margin: '0 5px' }} disabled={disabled}>
        <CameraIcon />
      </Button>
      <input
        type="file"
        name={id}
        accept=".png,.jpg,.jpeg,.bmp"
        id={id}
        onChange={onFileInputChange}
        disabled={disabled}
      />
    </div>
  );
};

const StyledUploadButton = styled(UploadButton)`
  position: relative;
  overflow: hidden;

  input[type='file'] {
    font-size: 100px;
    position: absolute;
    left: 0;
    top: 0;
    opacity: 0;
    visibility: hidden;
    cursor: pointer;
  }
`;

export default StyledUploadButton;
