import { observer } from 'mobx-react';
import * as React from 'react';
import styled from 'styled-components';

import translation from 'stores/translationStore';
import articlesStore, { ArticlesStore } from './store';

import ArticlesWrapper from './components/ArticlesWrapper';

interface IProps {
  className?: string;
}

/** Component Wrapper Styles */
const Wrapper = styled.div`
  margin-top: 30px;
  padding: 0 1px;
  h4 {
    font-weight: bold;
    text-transform: uppercase;
  }
`;

@observer
class Articles extends React.Component<IProps> {
  private articlesStore: ArticlesStore = articlesStore;

  public render() {
    if (this.articlesStore.disabled || this.articlesStore.articles.length <= 0) {
      return null;
    }

    return (
      <Wrapper className={this.props.className}>
        <h4>{translation.recommendedStories /* 'Talk of the town' */}</h4>
        <ArticlesWrapper
          articles={this.articlesStore.articles}
          isVerticalCards={this.articlesStore.isVerticalCards}
          showMoreButton={this.articlesStore.moreArticlesAvailable}
          onMoreButtonClick={this.articlesStore.showMoreArticles}
        />
      </Wrapper>
    );
  }
}

export default Articles;
