import * as React from 'react';
import styled from 'styled-components';

import Alert from '@vuukle/alert';
import Avatar from '@vuukle/avatar';
import Button from '@vuukle/button';
import ProtectedAuth from 'modules/Auth/ProtectedAuth';

import { IAlert } from 'stores/models/Alert';

import CommentModel from 'modules/CommentList/store/CommentItem';
interface IProps {
  comment: CommentModel;
  onSubmit: (e: React.FormEvent<HTMLFormElement>) => void;
  alert: IAlert | null;
  closeAlert: () => void;
  inProgress: boolean;
  closeEditor: () => void;
  children: any;
  authenticatedWithPassword: boolean;
}

const Wrapper = styled.div`
  display: flex;
  flex-direction: row;
  > div:first-child {
    flex: 0 0 40px;
    margin-right: 10px;
  }

  > div:last-child {
    flex: 1 1 100%;
    max-width: calc(100% - 50px);
  }
`;

class EditFormWrapper extends React.PureComponent<IProps> {
  /** Renders Alert */
  private renderAlert = (): React.ReactElement | null => {
    if (!this.props.alert) {
      return null;
    }
    return (
      <Alert type={this.props.alert.type} onClose={this.props.closeAlert}>
        {this.props.alert.message}
      </Alert>
    );
  };

  /** Renders available actions for users authenticated with password, otherwise we show auth */
  private renderAuthenticatedActions = () => {
    return (
      <div style={{ textAlign: 'right' }}>
        <Button
          disabled={this.props.inProgress}
          onClick={this.props.closeEditor}
          htmlType="button"
          type="subtle"
          style={{ margin: '0 5px' }}
        >
          Cancel
        </Button>
        <Button loading={this.props.inProgress} htmlType="submit">
          Save Edit
        </Button>
      </div>
    );
  };

  public render() {
    return (
      <Wrapper>
        <div>
          <Avatar
            src={this.props.comment.data.pictureUrl}
            name={this.props.comment.data.name}
            hash={this.props.comment.data.userId}
          />
        </div>
        <div>
          <form onSubmit={this.props.onSubmit}>
            {this.props.children}
            <div style={{ marginTop: '10px' }}>
              {this.renderAlert()}
              {/** Render authenticated actions only and only if user has been authorized using password. Otherwise we show protected auth module */}
              {this.props.authenticatedWithPassword && this.renderAuthenticatedActions()}
            </div>
          </form>
          {!this.props.authenticatedWithPassword && <ProtectedAuth onClose={this.props.closeEditor} />}
        </div>
      </Wrapper>
    );
  }
}

export default EditFormWrapper;
