import { observer } from 'mobx-react';
import * as React from 'react';

import Button from '@vuukle/button';
import Tag from '@vuukle/tag';
import translation from 'stores/translationStore';

import TextareaFooter from 'components/TextareaFooter';
import Textarea from './Textarea';

/** Props */
import ForeignEditStore from 'modules/EditCommentBox/stores/ForeignEditBox';
import ForeignStore from '../stores/ForeignBox';
import TransliterationStore, { Transliteration } from '../stores/models/Transliteration';

interface IProps {
  foreignStore: ForeignStore | ForeignEditStore;
}

@observer
class ForeignBox extends React.Component<IProps, {}> {
  public transliterationStore: Transliteration = TransliterationStore;

  constructor(props: IProps) {
    super(props);
  }

  public componentDidMount(): void {
    /**    Enabled transliteration for current component textarea */
    this.transliterationStore.toggleBox(this.props.foreignStore.id);

    /** Set value of the comment box if needed */
    const textarea = document.querySelector(`#${this.props.foreignStore.id}`);
    if (textarea instanceof HTMLTextAreaElement) {
      textarea.value = this.props.foreignStore.value;
    }
  }

  public componentWillUnmount(): void {
    /**  Remove textarea on component unmount */
    this.transliterationStore.toggleBox(this.props.foreignStore.id);
  }

  /** Handles for textarea change */
  private handleTextAreaChange = (e: React.FormEvent<HTMLTextAreaElement>): void => {
    this.props.foreignStore.value = e.currentTarget.value;
  };

  private renderContent(): React.ReactElement[] {
    return [
      <Textarea
        id={this.props.foreignStore.id}
        disabled={this.props.foreignStore.inProgress}
        rows={this.props.foreignStore.minimized ? 2 : 4}
        minRows={this.props.foreignStore.minimized ? 2 : 4}
        maxRows={10}
        onFocus={this.props.foreignStore.onFocus}
        onBlur={this.props.foreignStore.onBlur}
        onChange={this.handleTextAreaChange}
        placeholder={translation.common.writeComment /* Write Comment */}
        required={true}
        key={0}
      />,
      <TextareaFooter focused={false} key={1}>
        <div>
          <Button
            onClick={this.transliterationStore.toggleLanguage}
            style={{ padding: '0 6px', lineHeight: 1.2, marginRight: '5px', textTransform: 'capitalize' }}
            type="subtle"
            sm={true}
          >
            {this.transliterationStore.language}
          </Button>
        </div>
        <div>
          {/* !shouldShowLimitTag && <TextFormattingTips /> */}
          {this.props.foreignStore.allowedCharactersCount <= 200 && (
            <Tag>{this.props.foreignStore.allowedCharactersCount}</Tag>
          )}
        </div>
      </TextareaFooter>,
    ];
  }

  public render() {
    return <div>{this.renderContent()}</div>;
  }
}

export default ForeignBox;
