import * as React from 'react';
import styled from 'styled-components';

import Dropdown from '@vuukle/dropdown';
import Icon from '@vuukle/icon';
import { Menu } from '@vuukle/menu';
import { TabItem, Tabs } from '@vuukle/tabs';
import translation from 'stores/translationStore';

interface IProps {
  active: string;
  onSelect: (active: string) => void;

  className?: string;
}

const SortingRow: React.FunctionComponent<IProps> = ({ active, className, onSelect }) => {
  return (
    <Tabs className={className} active={active} onSelect={onSelect}>
      <span className="v-sorting-header">{translation.sorting.sortBy} </span>
      <TabItem name="get_latest" className="v-t-pane">
        {translation.sorting.latest /* Latest */}
      </TabItem>
      <TabItem name="get_most_up_votes" className="v-t-pane">
        {translation.sorting.mostLiked /* Best */}
      </TabItem>
      <TabItem name="get_top_comment" className="v-t-pane">
        {translation.sorting.editorsPick /* Editor's Pick */}
      </TabItem>
      <Dropdown
        align="right"
        className={active === 'get_most_replied' || active === 'get_reverse_chrono' ? `active v-t-pane` : `v-t-pane`}
        menu={
          <Menu>
            <Menu.Item
              onClick={() => onSelect('get_top_comment')}
              active={active === 'get_top_comment'}
              className="v-editors-pick"
            >
              {translation.sorting.editorsPick /* Editor's Pick */}
            </Menu.Item>
            <Menu.Item onClick={() => onSelect('get_most_replied')} active={active === 'get_most_replied'}>
              {translation.sorting.mostReplied /* Most Replies */}
            </Menu.Item>
            <Menu.Item onClick={() => onSelect('get_reverse_chrono')} active={active === 'get_reverse_chrono'}>
              {translation.sorting.oldest /* Oldest */}
            </Menu.Item>
          </Menu>
        }
      >
        <a href="#">
          {translation.sorting.more /* More */} <Icon type="caret" />
        </a>
      </Dropdown>
    </Tabs>
  );
};

const StyledSortingRow = styled(SortingRow)`
  .v-sorting-header {
    margin-right: 5px;
  }
  .v-editors-pick {
    display: none;
  }

  @media (max-width: 400px) {
    .v-sorting-header,
    .v-t-pane:nth-child(4) {
      display: none;
    }

    .v-editors-pick {
      display: block;
    }
  }
`;

export default StyledSortingRow;
