import { action, computed, decorate, observable } from 'mobx';
import path from 'ramda/src/path';

import { makeRequest } from '@vuukle/utils';
import { getToken, removeToken, removeUserApiKey, saveToken, setUserApiKey } from 'services/auth';
import translation from 'stores/translationStore';
import widgetStore from 'stores/widgetStore';

export class UserStore {
  /** Determines if user details are loading */
  public loading: boolean = false;
  /** User details we receive from server */
  public details: User | null = null;
  /**
   * User session token.
   * We need to save token not only in the cookies but here too, because some browsers block
   * 3rd party cookies (from iframe) and we can't be sure that created cookie will be available to use, but
   * we can't provide session for user with duration for at lest before page reloading
   * @default null
   */
  @observable
  private _token: string | null = null;

  constructor() {
    /** Authenticate user automatically is token exists */
    if (this.token) {
      this.authorizeSilently();
    }
  }

  /** Token getter */
  public get token(): string | null {
    const cookieToken = getToken();

    if (typeof cookieToken === 'string') {
      if (typeof this._token === 'string' && cookieToken !== this._token) {
        this._token = cookieToken;
      } else if (!this._token) {
        this._token = cookieToken;
      }
    }

    return this._token;
  }

  /** Token setter */
  public set token(token: string | null) {
    if (!token) {
      removeToken();
      this._token = null;
    } else {
      saveToken(token);
      this._token = token;
    }
  }

  /**
   * @description Determines if user is authorized
   * @return {boolean}
   */
  get isAuthorized(): boolean {
    return !!(this.details && this.details.id && this.details.email);
  }

  /**
   * @public
   * @description Get user details by token
   * @return {Promise<void>>}
   */
  public authorizeWithToken = async (): Promise<boolean> => {
    this.loading = true;

    try {
      const authResponse = await makeRequest(
        'POST',
        `${process.env.API_URL}/api/v1/Auth/me`,
        {},
        'application/json',
        this.token
      );
      if (
        authResponse.success &&
        typeof path(['data', 'email'], authResponse) === 'string' &&
        typeof path(['data', 'name'], authResponse) === 'string'
      ) {
        this.details = {
          avatar: authResponse.data.pictureUrl || null,
          email: authResponse.data.email,
          id: authResponse.data.userId,
          isModerator: authResponse.data.siteList.includes(widgetStore.article.host),
          isPasswordEntered: authResponse.data.isPasswordEntered,
          name: authResponse.data.name,
          points: authResponse.data.points,
        };
        setUserApiKey(authResponse.data.userId); // save user API key in cookies for analytics usage
        return true;
      }

      throw new Error(`[api/v1/Auth/me]. Response: ${JSON.stringify(authResponse)}. Token: ${this.token}`);
    } catch (err) {
      this.forgetUser();
      throw new Error(translation.messages.invalidLogin);
    } finally {
      this.loading = false;
    }
  };

  /**
   * @public
   * @description clear user session
   * @return {void}
   */
  public forgetUser = (): void => {
    removeUserApiKey();
    this.token = null;
    this.details = null;
  };

  /**
   * @public
   * @name authorizeSilently
   * @description Silent authorization method to do in background if token exists in cookies.
   * For example when we detected use token on page load
   * @return {void}
   */
  public authorizeSilently = async (): Promise<boolean> => {
    try {
      if (!this.token) {
        throw new Error('no_token');
      }
      await this.authorizeWithToken();
      return true;
    } catch (e) {
      return false;
    }
  };
}

decorate(UserStore, {
  authorizeSilently: action,
  authorizeWithToken: action,
  details: observable,
  forgetUser: action,
  isAuthorized: computed,
  loading: observable,
});

export default new UserStore();
