import { inject, observer } from 'mobx-react';
import * as React from 'react';

import EditFormWrapper from './components/EditFormWrapper';

import EnglishBox from 'modules/WriteComment/components/EnglishBox';
import ForeignBox from 'modules/WriteComment/components/ForeignBox';

import EnglishBoxStore from './stores/EnglishEditBox';
import ForeignBoxStore from './stores/ForeignEditBox';

/** Component Props */
import Comment from 'modules/CommentList/store/CommentItem';
interface IProps {
  /** Used for self closing. For example when user wants to cancel editing */
  onClose: () => void;
  comment: Comment;
}

/** Injected MobX Stores Props */
import { AuthStore, UserStore } from 'modules/Auth/stores';
import { WidgetStore } from 'stores/widgetStore';
interface IInjectedProps extends IProps {
  userStore: UserStore;
  authStore: AuthStore;
  widgetStore: WidgetStore;
}

@inject('userStore', 'authStore', 'widgetStore')
@observer
class EditCommentBox extends React.Component<IProps> {
  public store: EnglishBoxStore | ForeignBoxStore;

  constructor(props: IProps) {
    super(props);

    /** Create appropriate store based on language */
    if (this.injected.widgetStore.language === 'en') {
      this.store = new EnglishBoxStore(
        props.comment.data.id,
        props.comment.data.commentText,
        props.comment.editComment
      );
    } else {
      this.store = new ForeignBoxStore(
        props.comment.data.id,
        props.comment.data.commentText,
        props.comment.editComment
      );
    }
  }
  /** get Injected stores */
  get injected() {
    return this.props as IInjectedProps;
  }

  /**  Returns editor component based on selected language */
  private renderEditor(): React.ReactElement {
    /** Decide which box we need to show based on publisher configuration */
    if (this.store instanceof EnglishBoxStore) {
      return <EnglishBox boxStore={this.store} authorized={this.injected.userStore.isAuthorized} />;
    }

    return <ForeignBox foreignStore={this.store} />;
  }

  public render() {
    return (
      <EditFormWrapper
        comment={this.props.comment}
        onSubmit={this.store.onSubmit}
        alert={this.store.alert.value}
        closeAlert={this.store.alert.close}
        inProgress={this.store.inProgress}
        closeEditor={this.props.onClose}
        authenticatedWithPassword={
          (this.injected.userStore.details && this.injected.userStore.details.isPasswordEntered) || false
        }
      >
        {this.renderEditor()}
      </EditFormWrapper>
    );
  }
}

export default EditCommentBox;
