let translation = initialCustomText;
(() => {
  const customTextFromUrl = urlSearchParams.get('customText');
  if (!customTextFromUrl) {
    return;
  }
  try {
    translation = mergeDeepRight(initialCustomText, JSON.parse(customTextFromUrl));
  } catch (err) {
    console.warn('localization_text param is not correct JSON string'); // tslint:disable-line
  }
})();

export default translation;
