/**
 * @file Contains protected auth component. This component is used to show authorization module
 * for users that want to perform action that requires password authorization, but user is authenticated
 * without password.
 */
import uniqueId from 'lodash/uniqueId';
import { inject, observer } from 'mobx-react';
import * as React from 'react';

import Alert from '@vuukle/alert';
import Button from '@vuukle/button';
import EmailInput from './components/EmailInput';
import PasswordInput from './components/PasswordInput';
import StyledForm from './components/StyledForm';

import translation from 'stores/translationStore';

interface IProps {
  onClose: () => void;
}

interface IState {
  password: string;
}

/** MobX Store injected props */
import { AuthStore, UserStore } from 'modules/Auth/stores';
interface IInjectedProps {
  authStore: AuthStore;
  userStore: UserStore;
}

@inject('authStore', 'userStore')
@observer
class ProtectedAuth extends React.Component<IProps, IState> {
  private readonly id: string = uniqueId('pwd');
  public readonly state: Readonly<IState> = {
    password: '',
  };

  /**
   * @name injected
   * @description returns injected props by MobX
   * @return {Props & InjectedProps}
   */
  public get injected(): IProps & IInjectedProps {
    return this.props as IProps & IInjectedProps;
  }

  protected onSubmit = (e: React.FormEvent<HTMLFormElement>): void => {
    e.preventDefault();

    this.injected.authStore
      .signIn((this.injected.userStore.details && this.injected.userStore.details.email) || '', this.state.password)
      .then(() => {
        if (!this.injected.authStore.error) {
          this.setState({ password: '' });
        }
      });
  };

  /** Handles input change and writes to store based on input name attribute */
  private handleInputChange = (e: React.FormEvent<HTMLInputElement>): void =>
    this.setState({ [e.currentTarget.name as 'password']: e.currentTarget.value });

  public render() {
    const {
      authStore: { closeAlert, error, inProgress },
    } = this.injected;

    return (
      <StyledForm onSubmit={this.onSubmit}>
        <div>
          <p>{translation.messages.protectedAction}</p>
          <EmailInput
            id={this.id}
            value={(this.injected.userStore.details && this.injected.userStore.details.email) || ''}
            onChange={this.handleInputChange}
            disabled={true}
          />
          <PasswordInput id={this.id} value={this.state.password} minLength={0} onChange={this.handleInputChange} />
          {/* Errors Alert */}
          {error && (
            <Alert type="error" onClose={closeAlert}>
              {this.injected.authStore.error}
            </Alert>
          )}
          {/* Submit Button */}
          <div style={{ textAlign: 'right' }}>
            <p>
              <span style={{ margin: '0 5px' }}>
                Forgot password?{' '}
                <a href="https://user.vuukle.com/forgot-password" target="_blank" rel="nofollow">
                  {translation.messages.passwordReset}
                </a>
              </span>
            </p>
            <Button
              disabled={inProgress}
              htmlType="button"
              onClick={this.props.onClose}
              type="subtle"
              style={{ margin: '0 5px' }}
            >
              {translation.common.cancel}
            </Button>
            <Button loading={inProgress} htmlType="submit">
              {translation.common.signIn}
            </Button>
          </div>
        </div>
      </StyledForm>
    );
  }
}

export default ProtectedAuth;
