import { inject, observer } from 'mobx-react';
import * as React from 'react';
import styled from 'styled-components';

import SocialAuth from './components/SocialAuth';
import PasswordAuth from './PasswordAuth';
import PasswordlessAuth from './PasswordlessAuth';
import SignUp from './SignUp';
import SSO from './SSO';

interface IProps {
  className?: string;
}

/** MobX injected props */
import { AuthStore, UserStore } from 'modules/Auth/stores';
interface IInjectedProps extends IProps {
  authStore: AuthStore;
  userStore: UserStore;
}

interface IState {
  /**
   * Toggle item to know if needed to show sign up or sign in form,
   * as for password auth we have 2 views: sign in and sign up
   */
  showSignUp: boolean;
}

@inject('authStore', 'userStore')
@observer
class Auth extends React.Component<IProps, IState> {
  public readonly state: Readonly<IState> = {
    showSignUp: false,
  };

  constructor(props: IProps) {
    super(props);
  }

  /**
   * @name injected
   * @description just returns safety typed injected props
   * @returns {IInjectedProps}
   */
  get injected(): IInjectedProps {
    return this.props as IInjectedProps;
  }

  /**
   * @name toggleView
   * @protected
   * @description Toggles view for password auth configuration. So user can sign in or sign up
   * @returns {void}
   */
  public toggleView = (): void => this.setState({ showSignUp: !this.state.showSignUp });

  /**
   * @name renderVuukleAuth
   * @description Renders auth or sign up form based on configuration.
   * It can be password/non-password auth or sign up
   * @return {React.ReactElement<any>} SignUp or PasswordAuth, or Passwordless auth based on configuration
   */
  protected renderVuukleAuth = (): React.ReactElement<any> => {
    /** Render password less auth if auth with password is disabled */
    if (!this.injected.authStore.withPassword) {
      return <PasswordlessAuth />;
    } else if (this.state.showSignUp) {
      /** Render sign up in case if password auth is enabled and view is toggled */
      return <SignUp toggleView={this.toggleView} />;
    } else {
      /** Render password auth in case if password auth is enabled */
      return <PasswordAuth toggleView={this.toggleView} />;
    }
  };

  public render() {
    /** If user is authorized we don't need to show auth */
    if (this.injected.userStore.isAuthorized) {
      return null;
    }

    const { signInWithSocial, withVuukle, openSSO } = this.injected.authStore;

    const withSocial =
      this.injected.authStore.facebookEnabled ||
      this.injected.authStore.googleEnabled ||
      this.injected.authStore.twitterEnabled ||
      this.injected.authStore.disqusEnabled;

    return (
      <section className={this.props.className}>
        {/** Render social auth if configured */}
        <div>
          <SocialAuth
            facebook={this.injected.authStore.facebookEnabled}
            google={this.injected.authStore.googleEnabled}
            twitter={this.injected.authStore.twitterEnabled}
            disqus={this.injected.authStore.disqusEnabled}
            onClick={signInWithSocial}
          />
        </div>
        {/** Render Vuukle Auth if configured */}
        <div>{withVuukle && this.renderVuukleAuth()}</div>
        {/** Render SSO only button if everything else is disabled */}
        {!withVuukle && !withSocial && <SSO onClick={openSSO} />}
      </section>
    );
  }
}

const AuthStyled = styled(Auth)`
  margin-top: 10px;
  display: flex;
  justify-content: space-between;
  > div {
    &:first-child {
      min-width: 170px;
      max-width: 100%;
      flex-grow: 1;
    }
    &:last-child {
      flex-grow: 3;
      width: 100%;
      max-width: 65%;
    }
  }

  @media (max-width: 470px) {
    flex-direction: column;
    > div:last-child {
      margin-top: 10px;
      max-width: 100%;
    }
  }
`;

export default AuthStyled;
