import * as React from 'react';

interface IProps {
  id: string;
}

const PrivacyCheckbox: React.FunctionComponent<IProps> = ({ id }) => (
  <div style={{ textAlign: 'right' }}>
    <label htmlFor={`terms-${id}`}>
      <input type="checkbox" id={`terms-${id}`} name="terms" required={true} />
      <span>
        I agree with Vuukle's{' '}
        <a href="https://docs.vuukle.com/privacy-and-policy/" target="_blank" rel="noopener nofollow">
          Privacy Policy
        </a>
      </span>
    </label>
  </div>
);

export default PrivacyCheckbox;
