import * as React from 'react';

import Input from '@vuukle/input';
import translation from 'stores/translationStore';

interface IProps {
  id: string;
  value: string;
  onChange: (e: React.FormEvent<HTMLInputElement>) => void;
}

const NameInput: React.FunctionComponent<IProps> = ({ id, value, onChange }) => {
  const namePattern = '^[^~`!#$%@^&*+=[\\];,/{}|\\\\":<>?]*$';
  return (
    <Input
      type="text"
      value={value}
      pattern={namePattern}
      name="name"
      id={`name-${id}`}
      minLength={3}
      maxLength={40}
      placeholder={translation.common.name}
      dir="auto"
      title={translation.common.invalidName}
      onChange={onChange}
      required={true}
    />
  );
};

export default NameInput;
