import * as Cookies from 'js-cookie';
import { action, decorate, observable } from 'mobx';
import { recommendAPIs } from 'services/apis';
import { syncRecommendations } from 'services/communication';
import urlSearchParams from 'services/urlSearchParams';

export class WidgetStore {
  /** Article recommendation */
  public recommendCount: number = 0;
  public recommendInProgress: boolean = false;
  public isRecommended: boolean = false;
  /** Comments widget language */
  public language: string;
  /** Publisher API key */
  public apiKey: string;
  public commentingDisabled: boolean = false;
  /** Article Details */
  public article: {
    refHost: string;
    /** Article host */
    host: string;
    /** Article ID */
    id: string;
    /** Article title/heading */
    title: string;
    /** Article URL */
    url: string;
    /** Article image */
    img: string;
    /** Article tags */
    tags: string;
    /** Article author */
    author: string;
  };
  public theme: {
    /** Determines if widget is loaded on dark theme */
    darkMode: boolean;
    /** Primary widget color */
    color: string | null;
    /** Custom font-family */
    font: string | null;
  };

  /** Config we will use for transliteration */
  // transliterationConfig: Transliteration = TransliterationStore;
  constructor() {
    this.language = urlSearchParams.get('lang') || 'en';
    this.apiKey = urlSearchParams.get('apiKey') || '';

    this.article = {
      author: urlSearchParams.get('author') || '',
      host: urlSearchParams.get('host') || '',
      id: urlSearchParams.get('articleId') || '',
      img: urlSearchParams.get('img') || '',
      refHost: urlSearchParams.get('refHost') || '',
      tags: urlSearchParams.get('tags') || '',
      title: urlSearchParams.get('title') || '',
      url: urlSearchParams.get('url') || '',
    };

    this.theme = {
      color: urlSearchParams.get('color'),
      darkMode: urlSearchParams.get('darkMode') === 'true',
      font: urlSearchParams.get('font'),
    };

    if (process.env.NODE_ENV === 'development') {
      // this.apiKey = '7c2920a3-81a0-4758-8000-d2cc1b892ceb';
      // this.apiKey = 'de9d8063-9a97-4608-b612-9353a2b07c7b';
      // this.apiKey = '8f291d07-5577-46be-ae2e-b4782cc3a034'; // nbc-2
      // this.article = {
      //   // host: 'testertest.test.com',
      //   host: 'cranky-lumiere-e81cb7.netlify.com',
      //   id: '6698817',
      //   title: 'test title 610%!@#$%^&*()_+',
      //   // url: 'https://testertest.test.com',
      //   url: 'https://cranky-lumiere-e81cb7.netlify.com/',
      //   img: 'https://via.placeholder.com/400x400',
      //   tags: '',
      //   author: 'Author',
      // };
      // this.article = {
      //   host: 'nbc-2.com',
      //   id: '39104899',
      //   author: '',
      //   img: '',
      //   tags: 'News - AP-National',
      //   title: 'Trump Jr. mocks sexual assault claim against Kavanaugh',
      //   url: 'http://www.nbc-2.com/story/39104899/trump-jr-mocks-sexual-assault-claim-against-kavanaugh',
      // };
    }

    /** Check in cookies if user recommended this article */
    this.isRecommended = !!Cookies.get(`${this.article.host}&${this.article.id}_recommend`);
  }

  public recommend = async (): Promise<boolean | void> => {
    this.recommendInProgress = true;

    try {
      const alreadyVoted = this.isRecommended;
      /** Toggle recommendation */
      this.isRecommended = !this.isRecommended;
      /** If user already voted than decrease count of recommendations, remove cookie and send request */
      if (alreadyVoted) {
        if (this.recommendCount > 0) {
          this.recommendCount--;
        }
        Cookies.remove(`${this.article.host}&${this.article.id}_recommend`);
        /** Toggle recommend button in other widgets after we updated values */
        syncRecommendations(this.isRecommended, this.recommendCount);

        await recommendAPIs.delete();
      } else {
        /** Else increase counter, create cookie and send request */
        this.recommendCount++;
        Cookies.set(`${this.article.host}&${this.article.id}_recommend`, 'true', { expires: 30 });
        /** Toggle recommend button in other widgets after we updated values */
        syncRecommendations(this.isRecommended, this.recommendCount);

        await recommendAPIs.post();
      }
    } catch (error) {
      return false;
    } finally {
      this.recommendInProgress = false;
    }
  };
}

decorate(WidgetStore, {
  commentingDisabled: observable,
  isRecommended: observable,
  recommendCount: observable,
  recommendInProgress: observable,

  recommend: action,
});

export default new WidgetStore();
