import uniqueId from 'lodash/uniqueId';
import { inject, observer } from 'mobx-react';
import * as React from 'react';

import Alert from '@vuukle/alert';
import Button from '@vuukle/button';

import { AuthStore } from 'modules/Auth/stores';
import translation from 'stores/translationStore';

import EmailInput from './components/EmailInput';
import NameInput from './components/NameInput';
import PasswordInput from './components/PasswordInput';
import PrivacyCheckbox from './components/PrivacyCheckbox';
import StyledForm from './components/StyledForm';

interface IProps {
  toggleView: () => void;
}

interface IInjectedProps {
  authStore: AuthStore;
}

interface IState {
  /** Email input value */
  name: string;
  /** Email input value */
  email: string;
  /** Password input value */
  password: string;
}

@inject('authStore')
@observer
class SignUp extends React.Component<IProps, IState> {
  public readonly state: Readonly<IState> = {
    email: '',
    name: '',
    password: '',
  };
  private id: string = uniqueId('su');

  /**
   * @name injected
   * @description returns injected props by MobX
   */
  get injected(): IInjectedProps {
    return this.props as IProps & IInjectedProps;
  }

  /**
   * @name onSubmit
   * @protected
   * @description form submit event.
   * @param {React.FormEvent<HTMLFormElement>} e - From event
   * @returns {void}
   */
  protected onSubmit = (e: React.FormEvent<HTMLFormElement>): void => {
    e.preventDefault();

    this.injected.authStore.signUp(this.state.name, this.state.email, this.state.password).then(() => {
      if (!this.injected.authStore.error) {
        this.setState({ email: '', password: '', name: '' });
      }
    });
  };

  private handleInputChange = (e: React.FormEvent<HTMLInputElement>) =>
    this.setState({ [e.currentTarget.name as 'email']: e.currentTarget.value }); // FIXME: fix typings

  public render() {
    const {
      authStore: { closeAlert, error, inProgress },
    } = this.injected;

    return (
      <StyledForm onSubmit={this.onSubmit}>
        <NameInput id={this.id} value={this.state.name} onChange={this.handleInputChange} />
        <EmailInput id={this.id} value={this.state.email} onChange={this.handleInputChange} />
        <PasswordInput id={this.id} value={this.state.password} onChange={this.handleInputChange} />
        <PrivacyCheckbox id={this.id} />
        {/* Errors Alert */}
        {error && (
          <Alert type="error" onClose={closeAlert}>
            {this.injected.authStore.error}
          </Alert>
        )}
        {/* Submit Button */}
        <div style={{ textAlign: 'right' }}>
          <span style={{ margin: '0 5px' }}>
            Already have an account?{' '}
            <a href="#" onClick={this.props.toggleView}>
              {translation.common.signIn}
            </a>
          </span>
          <Button loading={inProgress} htmlType="submit">
            {translation.common.signUp}
          </Button>
        </div>
      </StyledForm>
    );
  }
}

export default SignUp;
